:root {
    --main-color:#fcb036;
    --second-color: #171f4a;
    --third-color: #000;
    --widget-bg: #f2f3f4;
    --white-color: #ffffff;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');


[data-theme=modern] {
    --primary: var(--main-color);
    --dt-primary-color: var(--main-color);
    --blue-avatar-bg: var(--main-color);
    --blue-avatar-color: var(--main-color);
    --blue: var(--main-color);
}

[data-theme=modern] .page-container{
    background-color: var(--widget-bg);
    position: relative;
    z-index: 0 !important;
}

/* [data-theme=modern] .indicator-pill, .indicator-pill-right {
    margin-top: 5px ;
} */

[data-theme=modern] .layout-main-section  {
   border: none !important;
    position: relative;
    z-index: 0 !important;
}

[data-theme=modern] .layout-main-section .frappe-list, .layout-main-section .report-wrapper {
    padding: 0rem !important;
}

[data-theme=modern] input[type="checkbox"]:checked {
    background-image: url("data:image/svg+xml, <svg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1 4.00001L2.66667 5.80001L7 1.20001' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/></svg>"),
     var(--checkbox-gradient) !important;
}

[data-theme=modern] ul li a:hover {
    background-color: #ffffff;
    color: #fcb036 !important;
    border-radius: 0.4rem;
    padding: 0.5em !important;
}

[data-theme=modern] body {
    font-size: 14px;
    background-color:#f2f3f4 !important;
    font-family: 'Roboto', sans-serif;
    position: relative;
  z-index: 0;
}

[data-theme=modern] a {
    color: #000;
    font-weight: 500;
}

[data-theme=modern] .small,
small {
    font-size: 95%;
}

[data-theme=modern] .h6.uppercase,
h6.uppercase {
    font-weight: 600;
    font-size: 11.5px;
}

[data-theme=modern] .standard-sidebar-item.selected {
    background-color: var(--main-color);
}

[data-theme=modern] .standard-sidebar-item.selected a {
    color: var(--white-color);
}

[data-theme=modern] .standard-sidebar-item.selected a svg {
    fill: var(--white-color);
}

[data-theme=modern] .standard-sidebar-item:hover {
    background-color: var(--second-color);
}



[data-theme=modern] .navbar {
    background-color:var(--main-color) !important;
